$primary-bg: #ffffff;
$text-color: #161616;
$logo: #53BFD2;
$secondary-bg: #F8F8F8;
.card {
    width: 1400px;
    height: 250px;
    display: flex;
    flex-direction: row;
    align-items: center;
    border-radius: 50px;

    .card-wrapper {
        // background: $primary-bg
        width: 1400px;
        height: 100px;
        border-radius: 150px;
        border: none;
        box-shadow: 0px 3px 12px #DEDEDE;
        display: flex;

        background: $secondary-bg;
        flex-direction: row;

        .item1 {

            svg {
                bottom: 8px;
                position: relative;
                left: 5px; } }
        .item2 {

            cursor: pointer;
            user-select: none;
            img {
                position: relative;
                bottom: 5px;
                left: 5px; } }
        .item3 {
            h3 {
                padding-left: 0px !important; }
            img {
                position: relative;
                right: 9px; }
            input {
                margin-left: 0px !important;
                font-size: 20px;
                color: #898989;
                font-family: 'Montserrat';
                font-weight: 600; } }

        .item4 {
            img {
                position: relative;
                bottom: 9px;
                left: 5px; } }

        .item1, .item2, .item3, .item4 {
            margin-left: 25px;
            margin-right: 25px;
            width: 250px;
            p {
                margin-left: 10px;
                font-size: 20px;
                color: #898989;
                font-family: 'Montserrat';
                font-weight: 600; }
            h3 {
                font-size: 18px;
                padding: 1em 1em 0 1em;
                color: $text-color;
                text-align: left;
                font-family: 'Montserrat';
                font-weight: 600; }
            .card-item {
                display: flex;
                align-items: center;
                position: relative;
                bottom: 5px;

                padding: 0 0.5em;
                input {
                    width: 100%;
                    background: transparent;
                    outline: none;
                    border: 0;
                    margin: 0 0.5em; }
                &:nth-child(3) {
                    width: 100%; }

                select {
                    background: transparent;
                    outline: none;
                    border: 0;
                    width: 60%; } } } }

    .item5 {
        display: flex;
        align-items: center;

        .circle_search {
            cursor: pointer;
            background: $logo;
            border-radius: 100px;
            width: 80px;
            height: 80px;
            position: absolute;
            right: 25px;
            display: flex;
            align-items: center;
            svg {
                display: flex;
                align-items: center;
                margin: 0 auto;
                font-size: 2em;
                color: $primary-bg; } } } }
.line {
    display: flex;
    align-items: center;
    justify-content: center; }

.box_3_active {
    display: block;
    position: relative;

    top: 10px;
    width: 340px;
    height: 240px;
    background: #fff;
    box-shadow: 1px 3px 12px #1616160D;
    opacity: 1;
    display: flex;
    flex-direction: column;

    .dd_row_one {
        display: flex;
        .option {
            .option_wapper {
                width: 170px;
                height: 120px;
                display: flex;
                align-items: center;
                flex-direction: column;
                justify-content: center;
                img {
                    width: 66px;
                    height: 58px; }
                h2 {
                    padding-top: 0.5em;
                    width: 140px;
                    height: 16px;
                    font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-600) var(--unnamed-font-size-14)/18px var(--unnamed-font-family-montserrat);
                    color: var(--unnamed-color-161616);
                    text-align: center;
                    font: normal normal 600 14px/18px Montserrat;
                    letter-spacing: 0.7px;
                    color: #161616;
                    text-transform: uppercase;
                    opacity: 1; } }
            &:hover {
                background: $logo; } } }

    .dd_row_two {
        flex: 1;
        display: flex;
        height: auto;
        background: #fff;
        .option {
            .option_wapper {
                width: 170px;
                height: 120px;
                display: flex;
                align-items: center;
                flex-direction: column;
                justify-content: center;
                transition: all .2s ease-in;
                img {
                    width: 66px;
                    height: 58px; }
                h2 {
                    padding-top: 0.5em;
                    width: 140px;
                    height: 16px;
                    font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-600) var(--unnamed-font-size-14)/18px var(--unnamed-font-family-montserrat);
                    color: var(--unnamed-color-161616);
                    text-align: center;
                    font: normal normal 600 14px/18px Montserrat;
                    letter-spacing: 0.7px;
                    color: #161616;
                    text-transform: uppercase;
                    opacity: 1; } }
            &:hover {
                background: $logo; } } } }
.box_3_inactive {
    display: none;
    opacity: 0; }
